






















import Vue from 'vue';
import { Component, Watch, Prop } from 'vue-property-decorator';

@Component
export default class extends Vue {
  @Prop(Array) menus;
  routeName:string = '';

  @Watch('$route')
  getRoute() {
    this.routeName = this.$route.name;
  }

  created() {
    this.getRoute();
    console.log(this.menus);
  }

  switchNav(name) {
    this.$router.push({ name: name });
  }
}



